import React from "react";
import { Card} from "react-bootstrap";
import {Link} from 'react-router-dom'

export default function Wrapper({ data }) {
  return (
    
    <div className='row text-center'>
      {data.map((item) => {
        return (
          // <Link to={`/recipe/${item.recipe.label}`}> // here is passing an string
          <Link to={{pathname:`recipe/${item.recipe.label}`, state:item}} className="col-md-4"> 
          {/* // here is passing an object, the state(key) is passing further the recipe component*/}
          <Card style={{ width: "50%" }}>
            <Card.Img variant='top' src={item.recipe.image} />
            <Card.Body>
              <Card.Title>{item.recipe.label}</Card.Title>
            </Card.Body>
          </Card>
          </Link>
        );
      })}
    </div>
  );
}
