import React, { useState, useEffect } from "react";
import Wrapper from "./Wrapper";
import "bootstrap/dist/css/bootstrap.min.css";
import Axios from "axios";
import { HashRouter, Route, Switch } from "react-router-dom";
import Recipe from "./Recipe";

export default function App() {
  const [data, setData] = useState([]);
  const [searchValue, setsearchValue] = useState("chicken"); // default search, will appear on useEffect

   useEffect(() => {
    //componentDidMount
    /* this will code execute only once */
    getData();
  }, []);

  // const getData = () => {
  //   fetch(
  //     `https://api.edamam.com/search?q=${searchValue}&app_id=${process.env.REACT_APP_ID}&app_key=${process.env.REACT_APP_API_KEY}`
  //   )
  //     .then(res => res.json())
  //     .then(result => {
  //       console.log(result);
  //       setData(result.hits)
  //     });
  // };

  const getData = () => {
    Axios.get(
      `https://api.edamam.com/search?q=${searchValue}&app_id=${process.env.REACT_APP_ID}&app_key=${process.env.REACT_APP_API_KEY}`
    ).then((result) => setData(result.data.hits));
  };

  const searchData = (e) => {
    e.preventDefault()
    getData();
  };

  return (
    <HashRouter>
      <Switch>
        <Route exact path='/'>
          {/* here is not using the selfclosing, because you need to assign a path, by default is './' --> it is saying to the browser that if the path is /, it will show all the data bellow.  */}

          <div className='container'>
            <div className='container bg-dark text-white'>
              <h1>Recipe APP</h1>
              <form className='text-right' onSubmit={searchData}>
                <label>
                  Search Recipe:
                  <input
                    type='text'
                    onChange={(e) => setsearchValue(e.target.value)}
                  />
                </label>
                <input type='submit' value='search' />
              </form>
            </div>
            <Wrapper data={data} />
          </div>
        </Route>
        {/* <Route path='/recipe/:productname' component={Recipe} /> */}
        {/* <Route path='/recipe/:productname' render={(props)=><Recipe {...props} data={data}/>}/> */}
        {/* render is an attribute in the Router */}
        {/* with {...props} spread all the props and send it, will hava a single objects and inside you have all the data */}
        <Route path="/recipe/:productname" component={Recipe}/>
        {/* self closing tag, is rendering this component */}
      </Switch>
    </HashRouter>
  );
}
